.project-tab {
    .card-body {
        padding: 60px 115px;
    }

    .repost {
        margin-top: 50px;
    }
}


.responsive-tabs {
    padding: 1rem;

    .nav-tabs {
        display: none;
    }
}

.nav-tabs {
    flex-wrap: nowrap;

    .nav-item {
        width: 100%;

        &::before {
            display: none;
        }
    }

    .nav-link {
        border-color: transparent;
        border-width: 5px;
        border: none;
        border-bottom:none;
        width: 100%;
        text-align: center;
        color: #5C5F65;
        font-size: 18px;
        font-weight: 500;
        position: relative;
        font-family: $font-secondary;
        &::before{
            content: "";
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0);
            width: 0;
            height: 3px;
            background-color: $primary-color;
            transition: .3s;
        }
        &.active {
            
            background-color: transparent;
            color: $primary-color;
            &::before{
                width: 100%;
            }
        }
        &:hover{
            color: #005BBB;
            &::before{
                width: 100%;
            }
        }
    }
}

@media (min-width: 992px) {
    .responsive-tabs .nav-tabs {
        display: flex;
    }

    .responsive-tabs {
        .card {
            border: none;
            border-radius: 0px 0px 24px 24px;
        }

        .card-header {
            display: none;
        }

        .collapse {
            display: block;
        }
    }


}

@media (max-width: 991px) {
    .responsive-tabs {
        .tab-pane {
            display: block !important;
            opacity: 1;
        }
    }
}

